// src/components/FocusItemCard.js
import React from 'react';
import { motion } from 'framer-motion';
import { useHandlers } from '../context/HandlerContext';
import { useSortable } from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';

const FocusItemCard = React.memo(
  ({ item, handleOpenFocusTest: _handleOpenFocusTest, ..._props }) => {
    const handlers = useHandlers();
    const { attributes, listeners, setNodeRef, transform, transition, isDragging } = useSortable({
      id: item.id,
    });

    const style = {
      transform: CSS.Transform.toString(transform),
      transition: transition || 'transform 0.2s cubic-bezier(0.22, 1, 0.36, 1)',
      willChange: 'transform',
      opacity: isDragging ? 0.7 : 1,
      background: isDragging ? '#f0f0f0' : undefined,
    };

    const handleCheckboxClick = (e) => {
      e.stopPropagation();
      handlers.toggleTestCompletion(item.subject, item.title, !item.completed);
    };

    const handleRemoveClick = (e) => {
      e.stopPropagation();
      handlers.removeFocusItem(item.id);
    };

    // Card classes inspired by a clean, timetable-like appearance
    const cardClasses = `
        flex items-center w-full p-4 rounded-lg border-l-4 transition-all duration-200 ease-in-out
        ${item.completed ? 'bg-green-100' : 'bg-gray-50'} hover:shadow-lg cursor-pointer
    `;

    const titleClasses = `
        font-bold text-gray-800 text-md
        ${item.completed ? 'line-through text-gray-400' : ''}
    `;

    // Animation variants for card
    const cardVariants = {
      initial: { opacity: 0, scale: 0.96, y: 10 },
      animate: { opacity: 1, scale: 1, y: 0 },
      exit: { opacity: 0, scale: 0.85, x: 40, transition: { duration: 0.18 } },
    };

    return (
      <motion.div
        ref={setNodeRef}
        style={style}
        className={cardClasses.replace('border-l-4', '')}
        variants={cardVariants}
        initial="initial"
        animate="animate"
        exit="exit"
      >
        <div className="flex items-center w-full">
          {/* Checkbox on the left */}
          <div className="flex items-center justify-center mr-4 flex-shrink-0">
            <motion.button
              onClick={handleCheckboxClick}
              className={`w-6 h-6 rounded-full border-2 ${item.completed ? 'bg-green-500 border-green-500' : 'border-gray-300 hover:border-blue-500'} flex-shrink-0 flex items-center justify-center transition-colors duration-200`}
              aria-label={item.completed ? 'Mark as incomplete' : 'Mark as complete'}
              whileTap={{ scale: 0.8 }}
              animate={{
                scale: item.completed ? 1.15 : 1,
                backgroundColor: item.completed ? '#22c55e' : '#fff',
                borderColor: item.completed ? '#22c55e' : '#d1d5db',
              }}
              transition={{ type: 'spring', stiffness: 350, damping: 18 }}
            >
              {item.completed && (
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  className="h-4 w-4 text-white"
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={3}
                    d="M5 13l4 4L19 7"
                  />
                </svg>
              )}
            </motion.button>
          </div>

          {/* Main Content as a link */}
          {item.link ? (
            <a
              href={item.link}
              target="_blank"
              rel="noopener noreferrer"
              className="flex-grow min-w-0 px-2 py-1 block focus:outline-none"
              tabIndex={0}
              aria-label={`Open test for ${item.title}`}
              style={{ textDecoration: 'none' }}
            >
              <h3 className={titleClasses} title={item.title}>
                {item.title}
              </h3>
              <p className="text-sm text-gray-500 truncate">{item.subject}</p>
              <div className="flex items-center mt-2 text-xs space-x-4">
                {item.mcqs > 0 && (
                  <span className="text-gray-600 flex items-center">{item.mcqs} MCQs</span>
                )}
              </div>
            </a>
          ) : (
            <div className="flex-grow min-w-0 px-2 py-1">
              <h3 className={titleClasses} title={item.title}>
                {item.title}
              </h3>
              <p className="text-sm text-gray-500 truncate">{item.subject}</p>
              <div className="flex items-center mt-2 text-xs space-x-4">
                {item.mcqs > 0 && (
                  <span className="text-gray-600 flex items-center">{item.mcqs} MCQs</span>
                )}
              </div>
            </div>
          )}

          {/* Action buttons on the right, always visible */}
          <div className="flex flex-col items-center space-y-1 ml-2 flex-shrink-0">
            <button
              className="p-1.5 rounded-full text-gray-400 hover:text-red-600 hover:bg-gray-100"
              onClick={handleRemoveClick}
              aria-label="Remove from focus"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
              >
                <line x1="18" y1="6" x2="6" y2="18"></line>
                <line x1="6" y1="6" x2="18" y2="18"></line>
              </svg>
            </button>
          </div>

          <span
            {...attributes}
            {...listeners}
            className="drag-handle flex items-center justify-center rounded-md ml-2 flex-shrink-0"
            style={{
              cursor: 'grab',
              width: 36,
              height: 36,
              touchAction: 'none',
              background: 'none',
            }}
            aria-label="Drag to reorder"
            tabIndex={0}
          >
            {/* SVG grip icon */}
            <svg
              width="20"
              height="20"
              viewBox="0 0 16 16"
              xmlns="http://www.w3.org/2000/svg"
              fill="#6B7280"
            >
              <path d="M9.5 13a1.5 1.5 0 1 1-3 0 1.5 1.5 0 0 1 3 0zm0-5a1.5 1.5 0 1 1-3 0 1.5 1.5 0 0 1 3 0zm0-5a1.5 1.5 0 1 1-3 0 1.5 1.5 0 0 1 3 0z" />
            </svg>
          </span>
        </div>
      </motion.div>
    );
  }
);

export default FocusItemCard;
